.logo {
  width: 142px;
  height: 33px;
  background-image: url(../../images/logo/logo.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

@media screen and (max-width: 934px) {
  .logo {
    margin-left: 19px;
  }
}

@media screen and (max-width: 675px) {
  .logo {
    width: 104px;
    height: 25px;
    margin-left: 27px;
  }
}
